@import url(https://fonts.googleapis.com/css?family=Poppins);
/* BASIC */

html {
    background-color:#050c30;
}
body {
    font-family: "Poppins", sans-serif;
    height: 100vh;
}
.index-page a {
    color: #92badd;
    display: inline-block;
    text-decoration: none;
    font-weight: 400;
}
.index-page h2 {
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    display: inline-block;
    margin: 40px 8px 10px 8px;
    color: #050c30;
}
.index-page h2.inactive {
    color: #ccc;
}
.index-page h2.active {
    color: #0d0d0d;
    border-bottom: 2px solid #5fbae9;
}
.index-page #logo {
    width: 130px !important;
    margin: 14px 2px !important;
}
.index-page .wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 100%;
    padding: 20px;
}
.index-page #formContent {
    border-radius: 10px 10px 10px 10px;
    background: #fff;
    padding: 30px;
    width: 90%;
    max-width: 450px;
    position: relative;
    padding: 0px;
    box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.3);
    text-align: center;
    margin: auto;
    margin-top: 25vh;
}
.index-page #formFooter {
    background-color: #f6f6f6;
    border-top: 1px solid #dce8f1;
    padding: 25px;
    text-align: center;
    border-radius: 0 0 10px 10px;
}
.index-page input[type=button], .index-page input[type=submit], .index-page input[type=reset] {
    background-color: #ec3b5c;
    border: none;
    color: white;
    padding: 15px 80px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    text-transform: uppercase;
    font-size: 13px;
    box-shadow: 0 10px 30px 0 rgba(95, 186, 233, 0.4);
    border-radius: 5px 5px 5px 5px;
    margin: 5px 20px 40px 20px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.index-page input[type=button]:hover, .index-page input[type=submit]:hover, .index-page input[type=reset]:hover {
    background-color: #bb233f;
}
.index-page input[type=button]:active, .index-page input[type=submit]:active, .index-page input[type=reset]:active {
    transform: scale(0.95);
}
.index-page input[type=text] {
    background-color: #f6f6f6;
    border: none;
    color: #0d0d0d;
    padding: 15px 32px;
    /* text-align: center; */
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    width: 85%;
    border: 2px solid #f6f6f6;
    transition: all 0.5s ease-in-out;
    border-radius: 5px 5px 5px 5px;
}
.index-page input[type=text]:focus {
    background-color: #fff;
    border-bottom: 2px solid #5fbae9;
}
.index-page input[type=text]:placeholder {
    color: #ccc;
}
.index-page .fadeInDown {
    animation-name: fadeInDown;
    animation-duration: 1s;
    animation-fill-mode: both;
}
.index-page .fadeIn {
    opacity: 0;
    animation: fadeIn ease-in 1;
    animation-fill-mode: forwards;
    animation-duration: 1s;
}
.index-page .fadeIn.first {
    animation-delay: 0.4s;
}
.index-page .fadeIn.second {
    animation-delay: 0.6s;
}
.index-page .fadeIn.third {
    animation-delay: 0.8s;
}
.index-page .fadeIn.fourth {
    animation-delay: 1s;
}
.index-page .underlineHover:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #56baed;
    content: "";
    transition: width 0.2s;
}
.index-page .underlineHover:hover {
    color: #0d0d0d;
}
.index-page .underlineHover:hover:after {
    width: 100%;
}
  
  @keyframes fadeInDown {
    0% {
      opacity: 0;
      transform: translate3d(0, -100%, 0);
    }
    100% {
      opacity: 1;
      transform: none;
    }
  }
  
  /* Simple CSS3 Fade-in Animation */
  @keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
  
  
