    right: 0;
    border-top-left-radius: 1rem;
    opacity: 0;
    border-bottom-left-radius: 1rem;
    z-index: -1;
    transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .nav-item:hover .nav-item-text::after {
